@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
.footer__inner, .footer__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.social, .footer__nav-list, .footer__contacts, .site-nav__list, .user-actions__link, .user-actions, .header__actions, .header__location-item, .header__location {
  display: flex;
  align-items: center;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.no-scroll {
  overflow: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #E11C4B;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #E11C4B;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1190px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 414px) {
  .container {
    padding: 0 10px;
  }
}

/* Components */
.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.header__location-wrapper {
  border-bottom: 2px solid #E8E8E8;
}
@media (max-width: 575px) {
  .header__location-wrapper {
    border: none;
  }
}
.header__location {
  height: 35px;
}
@media (max-width: 1199px) {
  .header__location {
    padding-top: 3px;
    height: 33px;
  }
}
@media (max-width: 575px) {
  .header__location {
    padding-top: 2px;
    margin-right: -3px;
    height: 62px;
    justify-content: flex-end;
  }
}
.header__location-item--city {
  margin-right: 36px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}
@media (max-width: 575px) {
  .header__location-item--city {
    margin-right: 0;
    order: 2;
  }
}
@media (max-width: 575px) {
  .header__location-item--shop {
    margin-right: 21px;
    order: 1;
  }
}
.header__location-icon {
  width: 18px;
  height: 18px;
  margin-right: 6px;
  flex-shrink: 0;
}
@media (max-width: 575px) {
  .header__location-icon {
    margin-right: 0;
    width: 24px;
    height: 24px;
  }
}
@media (max-width: 575px) {
  .header__location-text {
    display: none;
  }
}
.header__phone {
  display: inline-block;
  margin-left: auto;
  padding-left: 24px;
  padding-top: 4px;
}
@media (max-width: 1199px) {
  .header__phone {
    padding-top: 0;
  }
}
@media (max-width: 575px) {
  .header__phone {
    display: none;
  }
}
.header__actions {
  padding-top: 8px;
  padding-bottom: 4px;
  position: relative;
}
@media (max-width: 1199px) {
  .header__actions {
    padding-top: 9px;
    padding-bottom: 8px;
  }
}
@media (max-width: 575px) {
  .header__actions {
    padding-bottom: 18px;
  }
}
.header__logo {
  margin-right: 12px;
}
@media (max-width: 767px) {
  .header__logo {
    margin-right: 0;
  }
}
@media (max-width: 575px) {
  .header__logo {
    position: absolute;
    top: -56px;
    left: 0;
  }
}
.header__search-form {
  margin-top: -5px;
}
@media (max-width: 1199px) {
  .header__search-form {
    flex-grow: 1;
    margin-right: 12px;
    margin-top: -3px;
  }
}
@media (max-width: 991px) {
  .header__search-form {
    flex-grow: 0;
  }
}
@media (max-width: 767px) {
  .header__search-form {
    flex-grow: 1;
  }
}
@media (max-width: 575px) {
  .header__search-form {
    margin-right: 0;
  }
}
.header__user-actions {
  margin-left: auto;
}
@media (max-width: 1199px) {
  .header__user-actions {
    margin-bottom: 3px;
    margin-right: 6px;
  }
}
@media (max-width: 767px) {
  .header__user-actions {
    margin-right: 0;
  }
}
@media (max-width: 414px) {
  .header__user-actions {
    margin: 0;
  }
}

.contacts__link {
  display: inline-block;
  position: relative;
}
.contacts__link--phone:before, .contacts__link--mail:before {
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.contacts__link--phone:before {
  width: 18px;
  height: 18px;
  background-image: url(../images/phone.svg);
}
.contacts__link--mail:before {
  width: 25px;
  height: 17px;
  background-image: url(../images/envelope.svg);
}
@media (max-width: 767px) {
  .contacts__link--mail:before {
    width: 26px;
  }
}
@media (max-width: 414px) {
  .contacts__link--mail:before {
    width: 18px;
    height: 11px;
  }
}

.burger {
  display: none;
  padding: 0;
  margin-right: 14px;
  margin-bottom: 3px;
  background: none;
  border: 0;
  cursor: pointer;
  position: relative;
  font-size: 0;
  color: transparent;
}
@media (max-width: 575px) {
  .burger {
    display: block;
  }
}
.burger__item {
  display: block;
  width: 24px;
  height: 2px;
  margin-right: 1px;
  background-color: #8D8D8D;
  transition: transform 0.2s ease-out;
}
.burger__item:nth-child(2) {
  margin: 6px 0;
}

.logo__img {
  width: 227px;
  height: 70px;
}
@media (max-width: 1199px) {
  .logo__img {
    width: 190px;
    height: 59px;
  }
}
@media (max-width: 767px) {
  .logo__img {
    width: 180px;
    height: 56px;
  }
}

.search-form {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  border: 2px solid #DDDDDD;
  border-radius: 100px;
}
@media (max-width: 1199px) {
  .search-form {
    padding-top: 8px;
    padding-bottom: 8px;
  }
}
@media (max-width: 414px) {
  .search-form {
    padding-top: 6px;
    padding-bottom: 7px;
  }
}
.search-form__btn {
  width: 26px;
  height: 26px;
  padding: 0;
  margin-right: 5px;
  background: none;
  border: none;
  cursor: pointer;
}
@media (max-width: 414px) {
  .search-form__btn {
    width: 18px;
    height: 18px;
    margin-right: 6px;
  }
}
.search-form__btn-icon {
  width: 100%;
  height: 100%;
}
.search-form__input {
  width: 455px;
  border: none;
  outline: none;
}
.search-form__input::placeholder {
  color: #8D8D8D;
}
@media (max-width: 414px) {
  .search-form__input::placeholder {
    font-size: 14px;
  }
}
@media (max-width: 1199px) {
  .search-form__input {
    width: 100%;
  }
}
@media (max-width: 991px) {
  .search-form__input {
    width: 232px;
  }
}
@media (max-width: 767px) {
  .search-form__input {
    width: 100%;
  }
}

.user-actions__item:not(:last-child) {
  margin-right: 25px;
}
@media (max-width: 1199px) {
  .user-actions__item:not(:last-child) {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .user-actions__item:not(:nth-child(2)) {
    display: none;
  }
  .user-actions__item:nth-child(2) {
    margin-right: 0;
  }
}
@media (max-width: 575px) {
  .user-actions__item:nth-child(2) {
    position: absolute;
    top: -42px;
    right: 90px;
  }
}
.user-actions__link {
  flex-direction: column;
}
.user-actions__icon {
  width: 18px;
  height: 18px;
  margin-bottom: 2px;
}
@media (max-width: 1199px) {
  .user-actions__icon {
    width: 24px;
    height: 24px;
  }
}
@media (max-width: 1199px) {
  .user-actions__text {
    display: none;
  }
}

.site-nav {
  position: relative;
}
@media (max-width: 991px) {
  .site-nav {
    margin-right: -20px;
  }
}
.site-nav__item {
  margin-right: 36px;
}
.site-nav__item.swiper-slide {
  width: auto !important;
}
@media (max-width: 414px) {
  .site-nav__item {
    margin-right: 18px;
  }
}
.site-nav__link--active {
  color: #E11C4B;
}
.site-nav__link--promotion {
  padding-left: 24px;
  position: relative;
}
.site-nav__link--promotion:before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../images/promotion.svg);
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}
.site-nav__btn.swiper-button-prev, .site-nav__btn.swiper-button-next {
  display: block;
  width: 18px;
  height: 18px;
  margin: 0;
  padding: 0;
  background: #fff;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  z-index: 2;
  transform: translateY(-50%);
}
@media (max-width: 991px) {
  .site-nav__btn.swiper-button-prev, .site-nav__btn.swiper-button-next {
    display: none;
  }
}
.site-nav__btn--prev.swiper-button-prev {
  left: 0;
}
.site-nav__btn--next.swiper-button-next {
  right: 0;
}
.site-nav__btn.swiper-button-next.swiper-button-disabled, .site-nav__btn.swiper-button-prev.swiper-button-disabled {
  opacity: 0;
}
.site-nav__btn-icon {
  width: 100%;
  height: 100%;
}

.catalog-menu {
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.2s ease-out;
}
.catalog-menu--show {
  transform: translateX(0);
}
.catalog-menu__wrapper {
  padding-top: 48px;
}
.catalog-menu__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.17;
}
.catalog-menu__item {
  padding: 8px 0;
  position: relative;
}
.catalog-menu__item:before {
  content: "";
  width: calc(100% + 40px);
  height: 2px;
  margin-right: -20px;
  margin-left: -20px;
  background-color: #E8E8E8;
  position: absolute;
  bottom: 0;
  left: 0;
}
.catalog-menu__item:after {
  content: "";
  width: 8px;
  height: 15px;
  background-image: url(../images/arrow-icon.svg);
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.catalog-menu__link {
  display: flex;
  align-items: center;
}
.catalog-menu__img {
  width: 32px;
  height: 32px;
  margin-right: 8px;
}
.catalog-menu__btn-close {
  width: 18px;
  height: 18px;
  padding: 0;
  background: none;
  border: 0;
  cursor: pointer;
  position: absolute;
  top: 18px;
  right: 10px;
}
.catalog-menu__close-icon {
  width: 100%;
  height: 100%;
}

.promotions {
  padding-top: 36px;
  padding-bottom: 95px;
}
@media (max-width: 991px) {
  .promotions {
    padding-bottom: 71px;
  }
}
@media (max-width: 414px) {
  .promotions {
    padding-top: 25px;
    padding-bottom: 48px;
  }
}
.promotions__title {
  margin-bottom: 36px;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.15;
}
@media (max-width: 414px) {
  .promotions__title {
    margin-bottom: 24px;
    font-size: 24px;
  }
}
.promotions__list {
  display: flex;
  flex-wrap: wrap;
  gap: 34px;
}
@media (max-width: 991px) {
  .promotions__list {
    gap: 18px;
  }
}
.promotions__item {
  flex-basis: calc((100% - 68px) / 3);
  border-radius: 10px;
  box-shadow: 0px 6px 30px 4px rgba(32, 32, 32, 0.1);
}
@media (max-width: 991px) {
  .promotions__item {
    flex-basis: calc((100% - 18px) / 2);
  }
  .promotions__item:not(:nth-child(6)) {
    box-shadow: none;
  }
}
@media (max-width: 414px) {
  .promotions__item {
    flex-basis: 100%;
  }
  .promotions__item:nth-child(6) {
    box-shadow: none;
  }
}
.promotions__item-img {
  width: 100%;
  object-fit: cover;
}

.footer {
  margin-bottom: 37px;
  padding: 36px 0 18px;
  background-color: #F4F4F4;
  position: relative;
}
@media (max-width: 991px) {
  .footer {
    margin-bottom: 15px;
  }
}
@media (max-width: 414px) {
  .footer {
    padding-top: 25px;
    margin-bottom: 0;
  }
}
.footer__nav {
  margin-bottom: 41px;
}
@media (max-width: 991px) {
  .footer__nav {
    margin-bottom: 24px;
    flex-direction: column;
    align-items: flex-start;
  }
}
@media (max-width: 414px) {
  .footer__nav {
    margin-bottom: 15px;
  }
}
@media (max-width: 991px) {
  .footer__nav-list {
    margin-bottom: 23px;
  }
}
@media (max-width: 414px) {
  .footer__nav-list {
    margin-bottom: 17px;
  }
}
@media (max-width: 414px) {
  .footer__nav-item {
    font-size: 14px;
    line-height: 1.6;
  }
}
.footer__nav-item:not(:last-child) {
  margin-right: 62px;
}
@media (max-width: 991px) {
  .footer__nav-item:not(:last-child) {
    margin-right: 24px;
  }
}
@media (max-width: 414px) {
  .footer__nav-item:not(:last-child) {
    margin-right: 25px;
  }
}
.footer__contacts {
  padding-top: 1px;
}
@media (max-width: 414px) {
  .footer__contacts {
    flex-direction: column;
    align-items: flex-start;
  }
}
.footer__contacts-item:not(:last-child) {
  margin-right: 62px;
}
@media (max-width: 991px) {
  .footer__contacts-item:not(:last-child) {
    margin-right: 25px;
  }
}
@media (max-width: 414px) {
  .footer__contacts-item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 7px;
  }
}
@media (max-width: 414px) {
  .footer__contacts-item {
    font-size: 14px;
    line-height: 1.6;
  }
}
.footer__contacts-phone {
  padding-left: 29px;
}
@media (max-width: 414px) {
  .footer__contacts-phone {
    padding-left: 26px;
  }
}
.footer__contacts-mail {
  padding-left: 34px;
}
@media (max-width: 414px) {
  .footer__contacts-mail {
    padding-left: 26px;
  }
}
@media (max-width: 767px) {
  .footer__inner {
    flex-direction: column-reverse;
    align-items: flex-start;
  }
}
.footer__copyright {
  margin-bottom: 0;
  font-size: 14px;
  line-height: 1.4;
  color: #8D8D8D;
}
.footer__copyright span {
  text-transform: uppercase;
}
@media (max-width: 414px) {
  .footer__copyright {
    font-size: 12px;
    line-height: 1.5;
  }
}
.footer__social {
  margin-top: -4px;
}
@media (max-width: 991px) {
  .footer__social {
    position: absolute;
    top: 41px;
    right: 20px;
  }
}
@media (max-width: 767px) {
  .footer__social {
    margin-bottom: 24px;
    position: static;
  }
}

.social__item:not(:last-child) {
  margin-right: 12px;
}
.social__link {
  display: block;
  width: 40px;
  height: 40px;
}
@media (max-width: 575px) {
  .social__link {
    width: 30px;
    height: 30px;
  }
}
.social__icon {
  width: 100%;
  height: 100%;
}